import './Jobs.css'
import {GoPlus} from 'react-icons/go'
import {AiTwotoneEdit} from 'react-icons/ai'
import {IoIosArrowBack} from 'react-icons/io'
import {FiSearch} from 'react-icons/fi'
import React,{useState} from 'react'



function Jobs(){


    const [jobs,expandJobs] = useState(true);
    return(
        <div className={jobs?'jobView':'jobView-exp'}>
            <div className= 'job-TopView'>
                {/* <h4 className='jobBtn'>
                    Create a Job
                    </h4> */}
                     <div style={{display:'flex',justifyContent:'flex-start',alignItems:'center',paddingInline:'20px'}}>
                         <h2 className='jobTitle'>  
                            Jobs
                       </h2>
                       <div onClick={()=>jobs?expandJobs(false):expandJobs(true)} className={jobs?'expandBtn':'expandBtn-exp'}><IoIosArrowBack  size='30px'/> </div>

                    </div>

                <div style={{display:'flex',justifyContent:'space-evenly',alignItems:'center',padding:'10px'}}>
  
                   
  
                    <div style={{display:'flex',flex:1,justifyContent:'flex-start',paddingBottom:'0px'}}>
                    <div className='searchBtn'>
                        <FiSearch className='addJobBtn' size='18px'/>
                            <text>Search</text>
                        </div>
                    <div className='createBtn'>
                            <text>New</text>
                            <GoPlus className='addJobBtn' size='20px'/>
                        </div>
                        <div className='createBtn'>
                            <text>Edit</text>
                            <AiTwotoneEdit className='addJobBtn' size='20px'/>
                        </div>
                        
                                <div className={jobs?'screenBar':'screenBar-exp'} >
                                        <div className='screenText'>SCREEN</div>
                                        <div className='screenText' >HR INTERVIEW</div>
                                        <div className='screenText' >INTERVIEW</div>
                                        <div className='screenText' >ASSESSMENT</div>
                                        <div className='screenText' >OFFER</div>
                                        <div className='screenText' >ON HOLD</div>
                                        <div className='screenText' >REJECTED</div>
            
                            </div>
                        
                        
                    </div>
                    
                    
                    
                </div>
                
                <div className='animated-gradient'></div>
                </div>


                
                <div className={jobs?'jobList':'jobList-exp'}>
                    {allJobs.map((job)=>{
                        return(
                                    <div className='job-container'>
                                <div style={{display:'flex',height:jobs?'100px':'100px'}}>
                                    <div style={{display:'flex',flex:1,borderWidth:'2px',borderTopColor:'transparent',borderBottomColor:'transparent',padding:'10px',borderLeftColor:'transparent',borderRightColor:'#494B58',borderStyle:'solid'}}>
                                            
                                            <div style={{display:'flex',flexDirection:'column',justifyContent:'center',width: jobs?'200px':'400px'}}>
                                                <div style={{fontSize:jobs?'11px':'13px'}} className='job-loc'>{job.location}</div>
                                                <div className='job-title'>{job.name}</div>
                                            </div>
                                            
                                            {jobs?<></>:
                                                 
                                                    <div style={{display:'flex',justifyContent:'flex-start',alignItems:'center',width:'700px'}}>

                                                        <div className='screenBtn'><text>{job.stages[0]}</text></div>
                                                        <div className='screenBtn'><text>{job.stages[1]}</text></div>
                                                        <div className='screenBtn'><text>{job.stages[2]}</text></div>
                                                        <div className='screenBtn'><text>{job.stages[3]}</text></div>
                                                        <div className='screenBtn'><text>{job.stages[4]}</text></div>
                                                        <div className='screenBtn'><text>{job.stages[5]}</text></div>
                                                        <div className='screenBtn'><text>{job.stages[6]}</text></div>
                                                    
                                                   
                                                    </div>
   
                                                
                                                 
                                                 
                                   
                                            }
                                           
                                    </div>
                                   
                                    <div style={{display:'flex',flexDirection:'column',alignItems:'center',borderWidth:'2px'}}>
                                                <div style={{display:'flex',height:'50px',padding:'0px',justifyContent:'center',alignItems:'center'}} className='job-status'>
                                                <div style={{fontSize:jobs?'16px':'16px',paddingInline:'5px'}}>Hired</div>-
                                                <div><text style={{color:'dodgerblue',fontSize:'16px',paddingInline:'5px'}}>{job.status[0]}/{job.status[1]}</text></div>
                                                </div>
                                                
                                            <div  style={{display:'flex',flexDirection:'column',width:'140px',paddingTop:'5px',justifyContent:'center',alignItems:'center'}}  className='job-hired'>
                                                <div style={{display:'flex',justifyContent:'space-between',width:'150px'}}>
                                                    <text style={{fontSize:jobs?'11px':'13px',width:'50px',textAlign:'center'}}>Total</text>
                                                    <text style={{fontSize:jobs?'11px':'13px',width:'50px',textAlign:'center'}}>Unique</text>
                                                    <text style={{fontSize:jobs?'11px':'13px',width:'50px',textAlign:'center'}}>Parsed</text>
                                                </div>
                                            
                                            <div style={{display:'flex',justifyContent:'space-between',width:'150px'}}>
                                                
                                                <div className='Added-total' style={{color:'#5D90CC',fontSize:'16px',width:'50px',textAlign:'center'}}>{job.added[0]}</div>|
                                                <div className='Added-unique' style={{color:'#9DD31D',fontSize:'16px',width:'50px',textAlign:'center'}}>{job.added[1]}</div>|
                                                <div className='Added-unparsed' style={{color:'#F4515F',fontSize:'16px',width:'50px',textAlign:'center'}}>{job.added[2]}</div>
                                                
                                            </div>
                                            </div>
                                            
                                    </div>
                                </div>

                            </div>
                        )
                    })}
                    
                </div>
           
                
           
        </div>
        
    )
}

export default Jobs



const allJobs = [
    {id:0,name:'Machine Learning Engineer',status:[2,5],imp:true,location:'Bangalore',added:[19,17,0],stages:['-','2','-','2','-','-','-','-']},
    {id:1,name:'Application Development Analyst',status:[1,5],imp:true,location:'Delhi',added:[134,12,0],stages:['7','-','-','1','-','-','-','-']},
    {id:2,name:'Back End Developer',status:[4,5],imp:true,location:'Mumbai',added:[29,12,2],stages:['12','-','-','-','-','8','-','-']},
    {id:3,name:'Full Stack Engineer',status:[2,5],imp:true,location:'Hyderabad',added:[15,5,0],stages:['17','2','-','-','-','1','-','-']},
    {id:4,name:'Data Scientist',status:[3,5],imp:true,location:'Bangalore',added:[17,1,0],stages:['2','-','-','-','-','-','-','-']},
    {id:5,name:'Sales Recruiter',status:[5,5],imp:true,location:'Delhi',added:[67,7,0],stages:['6','-','-','-','-','-','-','-']},
]