import "./Dashboard.css";
import RecruiterChart from "./RecruiterChart";
import JobChart from "./JobChart";
import CandChart from "./CandChart";

function Dashboard() {
  return <div className="DashView">


    <div style={{display:'flex',flexDirection:'column'}}>
      <div style={{display:'flex',justifyContent:'space-evenly',flex:1}}>
          <div className='candidate'>
          <text>Candidate Report</text>
            <CandChart/>
          </div>

          <div className='job'>
            <text>Job Analysis</text>
            <JobChart />
          </div>
      </div>
     
      
      
      <div className='recruiter'>
      <text>Recruiter Activity</text>
        <RecruiterChart/>
      </div>
      
    </div>
  </div>;
}

export default Dashboard;
