import React,{useEffect, useState} from 'react'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faSlidersH} from '@fortawesome/free-solid-svg-icons'
import {IoIosArrowBack} from 'react-icons/io'
import './SideBar.css'
import { Rotate90DegreesCcw, RotateLeft } from '@material-ui/icons'

function SideBar(props){


      const [expand, setExpand] = useState(false);

      useEffect(()=>{
        setExpand(props.tab==='Dashboard'?true:false)
      })
 
        return(

              <div className={'mainview'} style={{}}>
    {/* <h5>Settings</h5> */}
          <div className='sidebar'>

                
                    <div style={{display:'flex',justifyContent:'space-between',alignItems:'center',padding:'20px'
                         }}>
                       <div onClick={()=>props.tab==='Dashboard'?props.setTab('Candidates'):props.setTab('Dashboard')} className={expand?'expandBtn-1-exp':'expandBtn-1'}><IoIosArrowBack  size='30px'/> </div>

                        <h2 style={{color:'white',marginLeft:'0px'}}>Dashboard</h2>
                         
                        <div className='icon-sidebar'>
                        <FontAwesomeIcon  size='lg' color='dodgerblue' icon={faSlidersH}>Settings</FontAwesomeIcon>
                        <div className='dropdown-content'>
                    <text>My Profile</text>
                    <text>Job Setup</text>
                    <text>Administration</text>
                    <text>Sourcing Setup</text>
                    <text>General</text>
                </div>
                        
                        </div>
                    </div>
            
                
          </div>
              <div className='sideview'>



              


                  <div style={{}} className='sideview-title'>Jobs</div>
                  <div className='border-line'></div>
                  <div className='card-view'>
                    <div style={{display:'flex',justifyContent:'space-evenly',alignItems:'center',padding:'10px'}}>
                      <div className='tile-btn' style={{display:'flex',flexDirection:'column',justifyContent:'center',alignItems:'center',width:'95px',height:'60px',height:'60px'}}><div className='info-1'>379</div><div className='info-2'>All Jobs</div></div>
                      <div className='tile-btn' style={{display:'flex',flexDirection:'column',justifyContent:'center',alignItems:'center',width:'95px',height:'60px'}}><div className='info-1'>123</div><div className='info-2'>Open</div></div>
                      <div className='tile-btn' style={{display:'flex',flexDirection:'column',justifyContent:'center',alignItems:'center',width:'95px',height:'60px'}}><div className='info-1'>30</div><div className='info-2'>Closed</div></div>
                    </div>
                  </div>

                  <div className='sideview-title'>Candidates</div>
                  <div className='border-line'></div>

                     <div className='card-view'>
                    <div style={{display:'flex',justifyContent:'space-evenly',alignItems:'center',padding:'10px'}}>
                      <div className='tile-btn' style={{display:'flex',flexDirection:'column',justifyContent:'center',alignItems:'center',width:'95px',height:'60px'}}><div className='info-1'>16356</div><div className='info-2'>Sourced</div></div>
                      <div className='tile-btn' style={{display:'flex',flexDirection:'column',justifyContent:'center',alignItems:'center',width:'95px',height:'60px'}}><div className='info-1'>10643</div><div className='info-2'>Unique</div></div>
                      <div className='tile-btn' style={{display:'flex',flexDirection:'column',justifyContent:'center',alignItems:'center',width:'95px',height:'60px'}}><div className='info-1'>5713</div><div className='info-2'>Duplicates</div></div>
                    </div>
                  </div>
                  <div className='sideview-title'>Resumes</div>
                  <div className='border-line'></div>
                  <div className='card-view'>
                    <div style={{display:'flex',justifyContent:'space-between',alignItems:'center',padding:'10px'}}>
                      <div className='tile-btn' style={{display:'flex',flexDirection:'column',justifyContent:'center',alignItems:'center',width:'95px',height:'60px'}}><div className='info-1'>0</div><div className='info-2'>Parsing</div></div>
                      <div className='tile-btn' style={{display:'flex',flexDirection:'column',justifyContent:'center',alignItems:'center',width:'95px',height:'60px'}}><div className='info-1'>718</div><div className='info-2'>Unparsed</div></div>
                      <div className='resolve-btn' style={{textAlign:'center',fontSize:'13px',color:"#7FA81F",height:'50px',display:'flex',alignItems:'center',width:'70px',marginLeft:'25px'}}>Resolve Unparsed</div>
                    </div>
                  </div>
                  
             </div>
          </div>
 

        )

    }


export default SideBar;








                  
