import React, { useState, useEffect } from "react";
import Footer from "./Footer";
import SideBar from "./SideBar";
import Icon from "./x.svg";
import "./App.css";
import "./Footer.css";
import MainView from "./MainView";
import Feed from "./Feed";
import Jobs from "./Jobs";
import {Route,Link,Switch, BrowserRouter as Router} from 'react-router-dom'







function App() {
  const [toggleState, setToggleState] = useState(true);
  const [tab, setTab] = useState("Dashboard");

  useEffect(() => {
    setTab(tab);
  });

  return (

    <div className="mainscreen">
      <div className="skillate">
        <img
          onClick={() =>
            toggleState ? setToggleState(false) : setToggleState(true)
          }
          className={toggleState ? "img-active" : "img"}
          src="https://res-2.cloudinary.com/crunchbase-production/image/upload/c_lpad,h_256,w_256,f_auto,q_auto:eco/mpils3yyqncbg8iptkrd"
        />
        <h1>X-Hire</h1>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "flex-start",
          alignItems: "flex-start",
        }}
      >
        <div className={toggleState ? "jobs" : "jobs-hidden"}>
          <Jobs />
        </div>
        <div className={toggleState ? "main" : "main-hidden"}>
          <MainView tab={tab} setTab={setTab} />
        </div>
        <div className={toggleState ? "side" : "side-hidden"}>
          <SideBar tab={tab} setTab={setTab} />
        </div>
      </div>
    </div>

  );
}

export default App;

