
import './MainView.css'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faTimes} from '@fortawesome/free-solid-svg-icons'
import React,{useState,useEffect} from 'react'
import Candidates from './Candidates'
import JobPage from './JobPage'
import Settings from './Settings'
import Dashboard from './Dashboard'
import {Route,Link,Switch,BrowserRouter as Router} from 'react-router-dom'
import {IoMdAdd} from 'react-icons/io'
import { LinkedCamera } from '@material-ui/icons'
import { Divider } from '@material-ui/core'
function MainView(props){
    const[tabs,setTabs] = useState(['Candidates','Approvals', 'Settings'])
    const[currentTab,setCurrentTab] = useState('Candidates')
    const[tabId,setTabId]=useState(2)
    const[loading,setLoading] = useState(true)
    
    useEffect(()=>{
        addTab(props.tab)
        setCurrentTab(props.tab)
        props.setTab(currentTab)
    })



    const addTab = (tabName) =>{
        if(tabs.includes(tabName)|| tabId===5){
            return;
        }
        setTabId(tabId+1);
        const tab = [tabName,...tabs,];
        setTabs(tab);
        setCurrentTab(tabName)
    }

    const delTab = (val)=>{
        if(tabId===0){
            return
        }
        setTabs(tabs.filter(e=>e!==val));
        if(val===currentTab){
            props.setTab(tabs[tabId]);

        }
  
        setTabId(tabId-1);

    }

    const loadTab=(currentTab)=>{
        switch(currentTab){
            case 'Candidates':
                return <Candidates/>
            case 'Jobs':
                return <JobPage/>
            case 'Dashboard':
                return <Dashboard/>
            case 'Settings':
                    return <Settings/>
            
            
                    default:
                return <></>
        }
    }




    return(
        <div className='container_mainview'>
            <div className='tab-bar'>
               
            </div>
            <div  className='tab-view'>

                        
                                                    
                        
                        <div style={{marginLeft:'15px',display:'flex',alignItems:'center'}}>
                            <div className='addTabIcon'><IoMdAdd className='tabIcon' size='30px'></IoMdAdd>
                                <div className='tab-options'>
                                        <div onClick={()=>addTab('Jobs')} className='option'>Jobs</div>
                                        <div onClick={()=>addTab('Candidates')} className='option'>Candidates</div>
                                        <div onClick={()=>addTab('Settings')} className='option'>Settings</div>
                                        <div onClick={()=>addTab('Approvals')} className='option'>Approvals</div>
                                        <div onClick={()=>addTab('Campaigns')} className='option'>Campaigns</div>
                                        <div onClick={()=>addTab('Reports')} className='option'>Reports</div>
                                        <div onClick={()=>addTab('Drives')} className='option'>Drives</div>
                                        <div onClick={()=>addTab('Analytics')} className='option'>Analytics</div>
                                        <div onClick={()=>addTab('Dashboard')} className='option'>Dashboard</div>
                                    </div> 
                            
                            </div>
                           
                               {tabs.map(label=>{
                                return(
                                    <div>
                                      
                                        
                                        <div   className={currentTab===label?'tab-container-active':'tab-container'}>
                                        <div onClick={()=>setCurrentTab(label)} className='tab-label'>{label}</div>
                                        <FontAwesomeIcon  onClick={()=>delTab(label)}  className={currentTab==label?'icon-closetab-active':'icon-closetab'}  size='md' color='white' icon={faTimes}/> 
                                    </div>
                                        
                                    
                                    
                                  
                                
                                </div>
                                )
                            })}
                        </div>
                            
                            
                        
            </div>
            
          
               {/* {loading?<div className="animated-gradient"></div>:<></>
}  */}




{loadTab(currentTab)}

        </div>
 
    )
}


export default MainView;