import './Settings.css'
import {CgProfile} from 'react-icons/cg'
import {RiAdminLine} from 'react-icons/ri'
import {VscSourceControl} from 'react-icons/vsc'
import {BsLayoutTextSidebarReverse} from 'react-icons/bs'
import React,{useState} from 'react'

function Settings(){

    const[menuTab,setMenuTab] = useState(0)

    return(
        <div style={{display:'block',justifyContent:'flex-start',alignItems:'flex-start'}} className='settings-main'>
           
           <div style={{display:'flex',justifyContent:'flex-start',height:'30px',marginLeft:'40px',marginTop:'20px'}}>
               <text style={{color:'#F4F4F4',fontSize:'30px'}}>Settings</text>
           </div>
           
           
           <div style={{display:'flex'}}>
               <div className='settings-menu'>
                   <div className='settings-menu-btn'>
                        <CgProfile color='#5D90CC' size={20}/>
                        <text>My Profile</text>
                        
                   </div>
                   <div className='settings-menu-btn'>
                        <RiAdminLine color='#8A82F5' size={20}/>
                        <text>Admin</text>
                        
                   </div>
                   <div className='settings-menu-btn'>
                        <VscSourceControl color='#ED8271' size={20}/>
                        <text>Sourcing</text>
                        
                   </div>
                   <div className='settings-menu-btn'>
                        <BsLayoutTextSidebarReverse color='#9DD31D' size={20}/>
                        <text>General</text>
                        
                   </div>
                   
                   
               </div>

               <div className='menu-view'>
                    <div className='settings-tab-bar'>
                        <div onClick={()=>setMenuTab(0)} className={menuTab===0?'settings-tab-bar-btn-active':'settings-tab-bar-btn'}>About you</div>
                        <div onClick={()=>setMenuTab(1)} className={menuTab===1?'settings-tab-bar-btn-active':'settings-tab-bar-btn'}>User Notifications</div>
                        <div onClick={()=>setMenuTab(2)} className={menuTab===2?'settings-tab-bar-btn-active':'settings-tab-bar-btn'}>Options</div>
                    </div>
                    <div className='menu-content'>
                        <div className='profile'>
                            
                            
                                <CgProfile size={80}/>
                                <text>Change your picture</text>
                        
                        </div>
                        <div className='menu-card'>
                            <text>First Name</text>
                            <input placeholder='first name' type='text'/>
                        </div>
                        <div className='menu-card'>
                            <text>Last Name</text>
                            <input  placeholder='last name' type='text'/>
                        </div>
                        <div className='menu-card'>
                            <text>Email</text>
                            <input  placeholder='email' type='text'/>
                        </div>
                        <div className='menu-card'>
                            <text>
                                Position
                            </text>
                            <input  placeholder='position' type='text'/>
                        </div>
                        <div className='menu-card'>
                            <text>
                                Office
                            </text>
                            <input placeholder='office' type='text'/>
                        </div>
                        <div className='menu-card'>
                            <text>
                                Phone
                            </text>
                            <input  placeholder='phone' type='text'/>
                        </div>
                        <div className='menu-card'>
                            <text>
                                Employee ID
                            </text>
                            <input  placeholder='employee id' type='text'/>
                        </div>
                        <div className='menu-card'>
                            <text>
                                Department
                            </text>
                            <input  placeholder='department' type='text'/>
                        </div>
                        <div className='menu-card'>
                            <text>
                                Language
                            </text>
                            <input  placeholder='language' type='text'/>
                        </div>
                    </div>
               </div>
           </div>
        </div>
    )
}


export default Settings