import {Bar} from 'react-chartjs-2'

function RecruiterChart(props){
    return(

        <div>
            <Bar
            data={{
                labels: ['Jan','Feb','Mar','Apr','May'],
                datasets: [{
                    label: 'Time Spent',
                    data: [12, 19, 3, 5, 2],
                    backgroundColor: [
                        'rgba(255, 99, 132, 0.2)',
                        
                    ],
                    borderColor: [
                        'rgba(255, 99, 132, 1)',
                     
                    ],
                    borderWidth: 1
                },{
                    label: 'Matching Engline Calls',
                    data: [5, 11, 7, 15, 12],
                    backgroundColor: [
 
                        'rgba(54, 162, 235, 0.2)',
                       
                    ],
                    borderColor: [
                  
                        'rgba(54, 162, 235, 1)',
                    ],
                    borderWidth: 1
                },
            {
                label: 'JD Assistant Calls',
                    data: [8, 9, 13, 7, 14],
                    backgroundColor: [
                    
                        'rgba(255, 206, 86, 0.2)',
               
                    ],
                    borderColor: [
                    
                        'rgba(255, 206, 86, 1)',
                 
                    ],
                    borderWidth: 1
            }]
            }}
            
            height={300}
            width={600}
            options={
                {
                maintainAspectRatio:false,
                scales:{
                    yAxes:[{
                        ticks:{
                            beginAtZero:true
                        }
                    }]
                }
            }}
            />
        </div>
    )
}


export default RecruiterChart