import "./Feed.css";
import React, { useState } from "react";
import { GrClose } from "react-icons/gr";
import { FiActivity } from "react-icons/fi";
import { ImProfile } from "react-icons/im";
import DateSlider from "./DateSlider";
import CandidateChart from './CandidateChart'
import CandidateChart2 from "./CandidateChart2";
function Feed(props) {
  const [tab, selectTab] = useState(0);
  const [edit, canEdit] = useState(false);

  return (
    <div className="feedView">
      
      <div style={{display:'flex'}}>
      
      <div style={{padding:'0px',width:'100%'}}>
      <div style={{ height: "40px" }} className="feedBG">
        <div className="feedBar">
          <div
            onClick={() => selectTab(0)}
            className={tab === 0 ? "feedBar-btn-active" : "feedBar-btn"}
          >
            <text>Resume</text>
          </div>
          <div
            onClick={() => selectTab(1)}
            className={tab === 1 ? "feedBar-btn-active" : "feedBar-btn"}
          >
            <text>Jobs</text>
          </div>
          <div
            onClick={() => selectTab(2)}
            className={tab === 2 ? "feedBar-btn-active" : "feedBar-btn"}
          >
            <text>About</text>
          </div>
          <div
            onClick={() => selectTab(3)}
            className={tab === 3 ? "feedBar-btn-active" : "feedBar-btn"}
          >
            <text>Activity</text>
          </div>
        </div>
        <div
          onClick={() => {
            props.show(false);
          }}
          className="closeIcon"
        >
          <GrClose size="15px" />
        </div>

        
      </div>

      <div className='feed-gradient'></div>
      
      <div style={{display:'flex'}}>
      
      <div className='detailContainer'>
      
      
        <div className='detail-card'>
        
               
                    <div style={{ display: "flex" }}>
                      <div className="score">
                        <div className="scoreNumber">#1</div>
                      </div>

                      <div
                        style={{
                          display: "block",
                          fontWeight: "500",
                          padding: "10px",
                        }}
                      >
                        <div
                          
                          className="candidate-name"
                        >
                          Rahul Kumar Jha
                          {/* <ImProfile className='resumeBtn' size='20px'/>  */}
                        </div>
                        <div className="college">
                          <text>Chandigarh Engineering College</text>
                          <text>Bangalore</text>
                        </div>
                        <div className="titles">
                          <text>{CandidateDetails.totalExp}</text>
                          <div
                            style={{
                              color: "grey",
                              marginTop: "-6px",
                              marginLeft: "10px",
                              marginRight: "10px",
                            }}
                          >
                            {"."}
                          </div>
                          <text>Full Stack Developer</text>
                          <div
                            style={{
                              color: "grey",
                              marginTop: "-6px",
                              marginLeft: "10px",
                              marginRight: "10px",
                            }}
                          >
                            {"."}
                          </div>
                          <text>Skillate</text>
                        </div>
                      

                    <div style={{}} >
                    
                    </div>
                  </div>
                </div>
                

                <div>
                    
                    

                    
                    
                  
                  
                </div>
              
        </div>
        
      



                {/* <div className='details'>
                  <div style={{color:'black',fontWeight:'500',fontSize:'17px'}}>Name</div>
                  <div><input className='inputBar' defaultValue={CandidateDetails.name}/></div>
                </div> */}

                
                
                  
                  
         
         

      <div style={{paddingTop:'20px',marginBottom:'80px'}}>
      <DateSlider/>
      </div>
      <CandidateChart2  score={[71, 67, 72, 76]} />
 
      </div>
      <Iframe iframe={iframe} />

      </div>
     
      



      </div>
      
     
      </div>
      

    </div>
  );
}

export default Feed;

const iframe =
  '<iframe src="https://docs.google.com/document/d/e/2PACX-1vT7A_LoNl8UD_LWtNK8RSfKHCC-J9jlZ7TWI-QdR4scyBYYmP5ANSSU4SeBxAXmJElYV669lrmc0rdI/pub?embedded=true" style="width:50vw; height:90vh;"></iframe>';
function Iframe(props) {
  return (
    <div
      dangerouslySetInnerHTML={{ __html: props.iframe ? props.iframe : "" }}
    />
  );
}


const CandidateDetails={
  name:'Rahul Kumar Jha',
  phone:'+91 8826898280',
  email:'rahulkumarjha26@gmail.com',
  social:'https://www.linkedin.com/in/rahul-j-743723118/',
  totalExp:'3 years',
  workExp:[
    {
      position:'QA Engineer',
      company: 'Cognizant Technologies Ltd',
      duration:'Apr 2020 - May 2021',
      loc:'Bangalore'
    }
  ],
  education:[
    {
      institution:'Chandigarh Engineering College',
      department:'Computer Science',
      degree:'BTECH',
      duration:'2017-2021',
    }
  ],
  skills:['JavaScript','React', 'React Native', 'Redux',
  'Java','JavaFX', 'MySQL', 'HTML', 'CSS', 'Python', 'Android', 'Open Cv', 'ML']

}