import React, { useState } from "react";
import './DateSlider.css'
export default function DateSlider() {

  let [id,setID] = useState(0)
  let [count, setCount] = useState(0);
  const [tab,setTab] = useState(0);
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        width:'100%',
        height:'100%',
        backgroundColor:'white',


        
      }}
    >
      <div style={{display:'flex',flexDirection:'row',justifyContent:'space-evenly',width:'400px'}}>
                <div onClick={()=>tab===0?setTab(1):setTab(0)} className={tab===0?'feed-btn-active':'feed-btn'} style={{display:'flex'}}><text>Education</text></div>
                <div onClick={()=>tab===0?setTab(1):setTab(0)} className={tab===1?'feed-btn-active':'feed-btn'}  style={{display:'flex'}}><text >Work Experience</text></div>
            </div>
      <div style={{display:'flex',justifyContent:'space-evenly',width:'100%'}}>
            
              <div
                style={{
                  
                  position: "relative",
                  marginBlock:'0px',
                  height:'150px',
                 
                  padding:'20px',
                  display:'flex',
                  borderRadius:'20px',
                  flexDirection:'column',
                  justifyContent:'space-between',
                  alignItems:'center'
              
                }}
              >
                <div style={{ display: "flex", alignItems: "center",justifyContent:'center' }}>
        <div
          style={{
            height: "30px",
            width: "30px",
            backgroundColor: "white",
            borderStyle: "solid",
            borderWidth: "2px",
            borderRadius: "100%",
            borderColor: "chartreuse",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <div style={{ color: "black", fontSize: "18px",textAlign:'center', fontWeight: "500" }}>
            0
          </div>
        </div>
        {
        tab===0?

        CandidateDetails.education.map((edu) => {
          return (
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <div
              className={id===edu.ID?'line-active':'line'}
              onClick={()=>setID(edu.ID)}
                style={{
                  width: 50 *  edu.duration,
                  height:'8px'
                }}
              >
                {" "}
              </div>
              <div
                style={{
                  height: "30px",
                  width: "30px",
                  backgroundColor: "white",
                  borderStyle: "solid",
                  borderWidth: "2px",
                  borderRadius: "100%",
                  borderColor: "chartreuse",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <div
                  
                  style={{
                    color: "black",
                    fontSize: "18px",
                    fontWeight: "500",
                  }}
                >
                  {(count += edu.duration)}
                </div>
              </div>
            </div>
          );
        })



        :
        
        
        
        
        
        
        
        CandidateDetails.workExp.map((work) => {
          return (
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <div
              className={id===work.ID?'line-active':'line'}
              onClick={()=>setID(work.ID)}
                style={{
                  width: 50 *  work.duration,
                  height:'8px'
                }}
              >
                {" "}
              </div>
              <div
                style={{
                  height: "30px",
                  width: "30px",
                  backgroundColor: "white",
                  borderStyle: "solid",
                  borderWidth: "2px",
                  borderRadius: "100%",
                  borderColor: "chartreuse",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <div
                  
                  style={{
                    color: "black",
                    fontSize: "18px",
                    fontWeight: "500",
                  }}
                >
                  {(count += work.duration)}
                </div>
              </div>
            </div>
          );
        })}
      </div>    
               <div className='slider-details'>

                  <div style={{display:'flex',justifyContent:'center',padding:'5px',width:'100%'}}>
                      <text style={{color:'dodgerblue',fontWeight:'500',fontSize:'16px'}}><text style={{color:'#2C3E50',fontWeight:'500',fontSize:'16px'}}>{tab===0?'Degree : ' : 'Location : '}</text>{tab===0? CandidateDetails.education[id].degree : CandidateDetails.workExp[id].loc}</text>
                    </div>
                    <div style={{display:'flex',justifyContent:'center',padding:'5px',width:'100%',alignItems:"center"}}>
                      <text style={{color:'dodgerblue',fontWeight:'500',fontSize:'16px'}}><text style={{color:'#2C3E50',fontWeight:'500',fontSize:'16px'}}>{tab===0?'School : ' : 'Role : '}</text>{tab===0? CandidateDetails.education[id].institution : CandidateDetails.workExp[id].position}</text>
                    </div>
                    <div style={{display:'flex',justifyContent:'center',padding:'5px',width:'100%'}}>
                      <text style={{color:'dodgerblue',fontWeight:'500',fontSize:'16px'}}><text style={{color:'#2C3E50',fontWeight:'500',fontSize:'16px'}}>{tab===0?'Department : ':'Company : '}</text>{tab===0? CandidateDetails.education[id].department: CandidateDetails.workExp[id].company}</text>
                    </div>
                    <div style={{display:'flex',justifyContent:'center',padding:'5px',width:'100%'}}>
                      <text style={{color:'dodgerblue',fontWeight:'500',fontSize:'16px'}}><text style={{color:'#2C3E50',fontWeight:'500',fontSize:'16px'}}>Duration : </text>{tab===0? CandidateDetails.education[id].duration :CandidateDetails.workExp[id].duration} Year</text>
                    </div>


               </div>
                
                
                <div>
                  
                </div>

                
              </div>
      </div>
      
      
      
     

      
    </div>
  );
}

