
import React from 'react'

import {Doughnut} from 'react-chartjs-2'

const CandidateChart=(props)=>{
    const data= {
    
        labels: ['Overall','Skills', 'Education', 'Experience', ],
        datasets: [{
            label: '',
            data: [props.score[0], props.score[1], props.score[2], props.score[3]],
            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)',
                'rgba(153, 102, 255, 0.2)',
                'rgba(255, 159, 64, 0.2)'
            ],
            borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
                'rgba(153, 102, 255, 1)',
                'rgba(255, 159, 64, 1)'
            ],
            borderWidth: 1
        }],
       
        options: {
            legend: {
                display: false,
                
            },
            scales: {
                y: {
                    beginAtZero: true
                }
            },
            tooltips: {
                callbacks: {
                   label: function(tooltipItem) {
                          return tooltipItem.yLabel;
                   }
                }
            }
        }
    };

    return <div style={{}}>
           
           <Doughnut data={data}
           height={100}
           width={200}
           options={{ maintainAspectRatio: true }}
       
            />
            <div style={{display:'flex',zIndex:-1,position:'absolute',left:'78px',bottom:'46px',height:'30px',width:'50px',alignItems:'center',justifyContent:'center',backgroundColor:'white',color:'black'}}>{props.score[0]}%</div>

         </div>

}

export default CandidateChart



