import {Line} from 'react-chartjs-2'

function JobChart(props){
    return(

        <div>
            <Line
            data={{
                labels: ['Source','Screen','Interview','Offer','Hire'],
                datasets: [{
                    label: 'Stage Completion in Days',
                    data: [5, 9, 11, 8, 14],
                    backgroundColor: [
 
                        '#B6EAEF',
                       
                    ],
                    borderColor: [
                  
                        '#5FE0ED',
                    ],
                    borderWidth: 1
                }]
            }}
            
            height={200}
            width={300}
            options={
                {
                maintainAspectRatio:false,
                scales:{
                    yAxes:[{
                        ticks:{
                            beginAtZero:true
                        }
                    }]
                }
            }}
            />
        </div>
    )
}


export default JobChart