import {Line} from 'react-chartjs-2'

function CandChart(props){
    return(

        <div>
            <Line
            data={{
                labels: ['Jan','Feb','Mar','Apr','May'],
                datasets: [{
                    label: 'Sourced Candidates',
                    data: [12, 19, 3, 5, 2],
                    backgroundColor: [
 
                        '#BFECA9',
                       
                    ],
                    borderColor: [
                  
                        '#9BEA81',
                    ],
                    borderWidth: 1
                }]
            }}
            
            height={200}
            width={300}
            options={
                {
                maintainAspectRatio:false,
                scales:{
                    yAxes:[{
                        ticks:{
                            beginAtZero:true
                        }
                    }]
                }
            }}
            />
        </div>
    )
}


export default CandChart